/* eslint-disable import/no-unresolved, no-console */

import React, { FormEvent } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useLocalStorage } from 'react-use';

import { evidence } from '../data';

import Collection from './Collection';
import { CollectionsList } from './Collection/collection-styled-components';
import { usePageTitle } from './shared/hooks';
import { EvidenceResultsWrapper } from './table-styled-components';

type CollectionType = {
  _id: string;
  name: string;
  items: string[];
};

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const CollectionsApp = () => {
  usePageTitle('Evidence Collections');
  const [isAdding, setIsAdding] = React.useState<boolean>(false);
  const [newName, setNewName] = React.useState<string>('');
  const [collections, setCollections] = useLocalStorage<CollectionType[]>('collections', []);

  const query = useQuery();
  const collectionItems = query.get('collectionItems');
  const name = query.get('name') || undefined;
  const id = query.get('id') || undefined;

  const items = React.useMemo(() => {
    console.log('running items', collectionItems, name, id);
    const itemIds = collectionItems?.split(',');
    if (itemIds) {
      return evidence.filter((item) => {
        return itemIds.indexOf(item._id) > -1;
      });
    }
    return [];
  }, [id, name, collectionItems]);
  const history = useHistory();

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setCollections([
      ...(collections as CollectionType[]),
      {
        _id: newName,
        name: newName,
        items: [],
      },
    ]);
    setIsAdding(false);
  };

  return (
    <div>
      <EvidenceResultsWrapper>
        <CollectionsList>
          <h4>Collections</h4>
          <div className="collections-list">
            {(collections as CollectionType[]).map((collection, i) => (
              <button
                type="button"
                className={`collection ${id === collection._id ? 'active' : ''}`}
                key={`collection-${collection.name}-${collection.items.length}`}
                onClick={() =>
                  history.push(
                    `/evidence/collections?id=${collection._id}&name=${
                      collection.name
                    }&collectionItems=${collection.items.join(',')}`
                  )
                }
              >
                {`${collection.name || `Collection ${i + 1}`} (${collection.items.length})`}
              </button>
            ))}
          </div>
          <div className="add-new">
            {!isAdding && (
              <button
                type="button"
                className="btn btn-primary btn-sm"
                onClick={() => setIsAdding(true)}
              >
                + Add New Collection
              </button>
            )}
            {isAdding && (
              <form onSubmit={handleSubmit}>
                <label htmlFor="newname" className="form-label form-label-sm">
                  Name
                  <input
                    id="newname"
                    type="text"
                    className="form-control form-control-sm"
                    value={newName}
                    onChange={(e) => setNewName(e.target.value)}
                  />
                </label>
                <div className="buttons">
                  <button
                    type="button"
                    className="btn btn-light btn-sm"
                    onClick={() => setIsAdding(false)}
                  >
                    Cancel
                  </button>
                  <button type="submit" className="btn btn-primary btn-sm">
                    Add
                  </button>
                </div>
              </form>
            )}
          </div>
        </CollectionsList>
        <h1>{name}</h1>
        <Collection id={id} name={name} items={items} />
      </EvidenceResultsWrapper>
    </div>
  );
};

export default CollectionsApp;
