import React from 'react';
import Modal from 'react-modal';
import { useParams, useHistory } from 'react-router-dom';

import type { Evidence } from '@jan6evidence/types';

import { evidence } from '../data';

import { Filters, Toggles } from '../types/Filters';

import EvidenceDetail from './EvidenceDetail';
import { EvidenceTable, EvidenceTimeline } from './EvidenceMain';
import EvidenceMapbox from './EvidenceMain/EvidenceMapbox';
import FilterBox from './FilterBox';
import filterEvidence from './FilterBox/filterEvidence';
import { HighlightContext } from './shared/contexts';
import { usePageTitle } from './shared/hooks';
import { debounce } from './shared/util';
import {
  EvidenceResultsWrapper,
  EvidenceTableWrapper,
  EvidenceCount,
} from './table-styled-components';

Modal.setAppElement('#root');

const EvidenceApp = () => {
  usePageTitle('Evidence Explorer');

  const [filters, setFilters] = React.useState<Filters>({
    location: new Set<string>(),
    suspect: new Set<string>(),
    otherTags: new Set<string>(),
  });
  const [keywords, setKeywords] = React.useState<string[]>([]);
  const [toggles, setToggles] = React.useState<Toggles>({
    includeUntimestamped: true,
  });
  const [highlight, setHighlight] = React.useState<string>('');
  const [startEndTimes, setStartEndTimes] = React.useState<[Date, Date] | null>(null);
  const { detailId } = useParams<{ detailId?: string }>();
  const history = useHistory();
  const closeModal = React.useCallback(() => history.push(`/evidence`), [history]);

  const { filteredEvidence, timelineFilteredEvidence, filterChoices } = filterEvidence(
    evidence,
    filters,
    keywords,
    toggles,
    startEndTimes
  );

  const debouncedHighlight = React.useCallback(debounce(setHighlight, 100), []);

  let prevEvidence;
  let nextEvidence;
  const detailEvidence = detailId
    ? filteredEvidence.find((r, i) => {
        if (r._id === detailId) {
          prevEvidence = filteredEvidence[i - 1];
          nextEvidence = filteredEvidence[i + 1];
          return true;
        }
        return false;
      })!
    : { _id: '' };

  return (
    <div>
      <FilterBox
        onFilterChanged={(section, newContent) => {
          setFilters((oldFilters) => ({ ...oldFilters, [section]: newContent }));
        }}
        onKeywordsChanged={(newKeywords) => setKeywords(newKeywords)}
        filters={filters}
        choices={filterChoices}
        keywords={keywords}
        toggles={toggles}
        onToggleChanged={(toggle, newValue) => {
          setToggles((oldToggles) => ({ ...oldToggles, [toggle]: newValue }));
        }}
      />
      <EvidenceResultsWrapper>
        <h1>Evidence Database</h1>
        <HighlightContext.Provider value={{ highlight, setHighlight: debouncedHighlight }}>
          <EvidenceMapbox
            evidence={filteredEvidence}
            onHover={debouncedHighlight}
            highlight={highlight}
          />
          <EvidenceTimeline
            startEndTimes={startEndTimes}
            onChangeTimes={setStartEndTimes}
            // We re-filter here, ignoring time time filter, so that we still show
            // evidence on the timeline that isn't in the selected time range
            evidence={timelineFilteredEvidence}
          />
          <EvidenceCount>{filteredEvidence.length} bits of evidence</EvidenceCount>
          <EvidenceTableWrapper>
            <EvidenceTable
              results={filteredEvidence}
              highlight={highlight}
              onHover={debouncedHighlight}
            />
          </EvidenceTableWrapper>
        </HighlightContext.Provider>
      </EvidenceResultsWrapper>
      <EvidenceDetail
        isOpen={!!detailId}
        evidence={detailEvidence}
        onClose={closeModal}
        prev={prevEvidence}
        next={nextEvidence}
      />
    </div>
  );
};

export default EvidenceApp;
